
import React, { useState, useEffect } from 'react'
import hint from './img/hint.svg'
import closeModalIcon from '../../assets/img/close-modal-icon.svg'
import './hint.less'
import { DeriEnv } from '../../lib/web3js/shared/config/env';
import { inject, observer } from 'mobx-react'
function Announcement({wallet}) {
  const [show, setShow] = useState(true)
  const closeHint = () => {
    sessionStorage.setItem('bsc-hint', false)
    setShow(false)
  }
  useEffect(() => {
    let sess = sessionStorage.getItem('bsc-hint')
    if (sess !== null) {
      if (sess) {
        setShow(false)
      }
    }
  }, [])
  return (
    DeriEnv.get() === 'testnet' ? <div>
      {show && <div className='trade-hint no-vip'>
        <div className='hint-img'>
          <img src={hint} alt='' />
        </div>
        <div className='hint-box'>
          <div className='hint-title'>
            <span>
              For Your Information
            </span>
            <img className='close' src={closeModalIcon} alt='X' onClick={closeHint} />
          </div>
          <div className='hint-content'>
            <div className='hint-text'>
              You are visting the testnet website. The mainnet website is <a rel='noreferror' target='_blank' href='https://deri.io/' >https://deri.io/.</a>
              {/* Services on Deri Protocol (BNBChain) have been paused while Venus prepares for the relaunch.  <a rel='noreferror' target='_blank' href='https://deri.io/' >Learn more.</a> */}
            </div>
            <div className='hint-link'>
              <a rel='noreferrer' target='_blank' href='https://deri.io/' >Switch to the mainnet.</a>
            </div>
          </div>
        </div>
        <div className='m-check' onClick={closeHint}>
          <span className='check'>
            OK
          </span>
        </div>
      </div>}

    </div> : null
  )
}
export default inject('wallet')(observer(Announcement))